body{
    background-color:#21f3b361;
    font-family: 'Barlow Condensed', sans-serif;
  
}
#root{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%,-50%);
    
}
#date{
    padding-left: 20px;
    border: 10px solid #34908387;
    padding-right: 20px;
    box-shadow: 0px 2px 12px 8px #81aea8d9;
}
#date p{
    font-size: 100px;
    font-weight: bolder;
    color: #bb26caab;
}
#title{
    text-align: center;
    font-size: 80px;
    color: #ff00719c;
}